import Test from './vexflow_test_helpers';
import { Bend, Formatter, ModifierContext, TabNote, TabStave, TickContext } from '../src';

declare const QUnit: any;
declare function ok(...args);

export const BendTests = (function() {
  return {
    Start: function() {
      QUnit.module('Bend');
      Test.runTests('Double Bends', this.doubleBends.bind(this));
      Test.runTests('Reverse Bends', this.reverseBends.bind(this));
      Test.runTests('Bend Phrase', this.bendPhrase.bind(this));
      Test.runTests('Double Bends With Release', this.doubleBendsWithRelease.bind(this));
      Test.runTests('Whako Bend', this.whackoBends.bind(this));
    },

    doubleBends: function(options, contextBuilder) {
      const ctx = new contextBuilder(options.elementId, 500, 240);
      ctx.scale(1.5, 1.5); ctx.fillStyle = '#221'; ctx.strokeStyle = '#221';
      ctx.setRawFont(' 10pt Arial');
      const stave = new TabStave(10, 10, 450)
        .addTabGlyph().setContext(ctx).draw();

      function newNote(tab_struct) { return new TabNote(tab_struct); }
      function newBend(text) { return new Bend(text); }

      const notes = [
        newNote({
          positions: [{ str: 2, fret: 10 }, { str: 4, fret: 9 }], duration: 'q',
        })
          .addModifier(newBend('Full'), 0)
          .addModifier(newBend('1/2'), 1),

        newNote({
          positions: [{ str: 2, fret: 5 }, { str: 3, fret: 5 }], duration: 'q',
        })
          .addModifier(newBend('1/4'), 0)
          .addModifier(newBend('1/4'), 1),

        newNote({
          positions: [{ str: 4, fret: 7 }], duration: 'h',
        }),
      ];

      Formatter.FormatAndDraw(ctx, stave, notes);
      notes.forEach(function(note) {
        Test.plotNoteWidth(ctx, note, 140);
      });

      ok(true, 'Double Bends');
    },

    doubleBendsWithRelease: function(options, contextBuilder) {
      const ctx = new contextBuilder(options.elementId, 550, 240);
      ctx.scale(1.0, 1.0);
      ctx.setBackgroundFillStyle('#FFF');
      ctx.setFont('Arial', Test.Font.size);
      const stave = new TabStave(10, 10, 550)
        .addTabGlyph().setContext(ctx).draw();

      function newNote(tab_struct) { return new TabNote(tab_struct); }
      function newBend(text, release) { return new Bend(text, release); }

      const notes = [
        newNote({
          positions: [{ str: 1, fret: 10 }, { str: 4, fret: 9 }], duration: 'q',
        })
          .addModifier(newBend('1/2', true), 0)
          .addModifier(newBend('Full', true), 1),

        newNote({
          positions: [{ str: 2, fret: 5 },
            { str: 3, fret: 5 },
            { str: 4, fret: 5 }], duration: 'q',
        })
          .addModifier(newBend('1/4', true), 0)
          .addModifier(newBend('Monstrous', true), 1)
          .addModifier(newBend('1/4', true), 2),

        newNote({
          positions: [{ str: 4, fret: 7 }], duration: 'q',
        }),
        newNote({
          positions: [{ str: 4, fret: 7 }], duration: 'q',
        }),
      ];

      Formatter.FormatAndDraw(ctx, stave, notes);
      notes.forEach(function(note) {
        Test.plotNoteWidth(ctx, note, 140);
      });
      ok(true, 'Bend Release');
    },

    reverseBends: function(options, contextBuilder) {
      const ctx = new contextBuilder(options.elementId, 500, 240);

      ctx.scale(1.5, 1.5); ctx.fillStyle = '#221'; ctx.strokeStyle = '#221';
      ctx.setRawFont('10pt Arial');
      const stave = new TabStave(10, 10, 450)
        .addTabGlyph().setContext(ctx).draw();

      function newNote(tab_struct) { return new TabNote(tab_struct); }
      function newBend(text) { return new Bend(text); }

      const notes = [
        newNote({
          positions: [{ str: 2, fret: 10 }, { str: 4, fret: 9 }], duration: 'w',
        })
          .addModifier(newBend('Full'), 1)
          .addModifier(newBend('1/2'), 0),

        newNote({
          positions: [{ str: 2, fret: 5 }, { str: 3, fret: 5 }], duration: 'w',
        })
          .addModifier(newBend('1/4'), 1)
          .addModifier(newBend('1/4'), 0),

        newNote({
          positions: [{ str: 4, fret: 7 }], duration: 'w',
        }),
      ];

      for (let i = 0; i < notes.length; ++i) {
        const note = notes[i];
        const mc = new ModifierContext();
        note.addToModifierContext(mc);

        const tickContext = new TickContext();
        tickContext.addTickable(note).preFormat().setX(75 * i);

        note.setStave(stave).setContext(ctx).draw();
        Test.plotNoteWidth(ctx, note, 140);
        ok(true, 'Bend ' + i);
      }
    },

    bendPhrase: function(options, contextBuilder) {
      const ctx = new contextBuilder(options.elementId, 500, 240);
      ctx.scale(1.5, 1.5); ctx.fillStyle = '#221'; ctx.strokeStyle = '#221';
      ctx.setRawFont(' 10pt Arial');
      const stave = new TabStave(10, 10, 450).addTabGlyph().setContext(ctx).draw();

      function newNote(tab_struct) { return new TabNote(tab_struct); }
      function newBend(phrase) { return new Bend(null, null, phrase); }

      const phrase1 = [
        { type: Bend.UP, text: 'Full' },
        { type: Bend.DOWN, text: 'Monstrous' },
        { type: Bend.UP, text: '1/2' },
        { type: Bend.DOWN, text: '' },
      ];
      const bend1 = newBend(phrase1).setContext(ctx);

      const notes = [
        newNote({
          positions: [{ str: 2, fret: 10 }], duration: 'w',
        })
          .addModifier(bend1, 0),
      ];

      for (let i = 0; i < notes.length; ++i) {
        const note = notes[i];
        const mc = new ModifierContext();
        note.addToModifierContext(mc);

        const tickContext = new TickContext();
        tickContext.addTickable(note).preFormat().setX(75 * i);

        note.setStave(stave).setContext(ctx).draw();
        Test.plotNoteWidth(ctx, note, 140);
        ok(true, 'Bend ' + i);
      }
    },

    whackoBends: function(options, contextBuilder) {
      const ctx = new contextBuilder(options.elementId, 400, 240);
      ctx.scale(1.0, 1.0); ctx.setBackgroundFillStyle('#FFF');
      ctx.setFont('Arial', Test.Font.size);
      const stave = new TabStave(10, 10, 350).addTabGlyph().setContext(ctx).draw();

      function newNote(tab_struct) { return new TabNote(tab_struct); }
      function newBend(phrase) { return new Bend(null, null, phrase); }

      const phrase1 = [
        { type: Bend.UP, text: 'Full' },
        { type: Bend.DOWN, text: '' },
        { type: Bend.UP, text: '1/2' },
        { type: Bend.DOWN, text: '' },
      ];

      const phrase2 = [
        { type: Bend.UP, text: 'Full' },
        { type: Bend.UP, text: 'Full' },
        { type: Bend.UP, text: '1/2' },
        { type: Bend.DOWN, text: '' },
        { type: Bend.DOWN, text: 'Full' },
        { type: Bend.DOWN, text: 'Full' },
        { type: Bend.UP, text: '1/2' },
        { type: Bend.DOWN, text: '' },
      ];

      const notes = [
        newNote({
          positions: [{ str: 2, fret: 10 }, { str: 3, fret: 9 }], duration: 'q',
        })
          .addModifier(newBend(phrase1), 0)
          .addModifier(newBend(phrase2), 1),
      ];

      Formatter.FormatAndDraw(ctx, stave, notes);
      Test.plotNoteWidth(ctx, notes[0], 140);
      ok(true, 'Whako Release');
    },
  };
})();
